.team__block-container {
  display: grid;
  place-items: center;
  overflow: hidden;
  width: 100%;
}

.team__block-image {
  position: relative;
}

.team__block-image img {
  border-radius: 10px;
  object-fit: cover;
  height: 360px;
  width: 270px;
}

.team__block-name {
  font-family: Montserrat;
  font-weight: 600;
  font-size: 24px;
  line-height: 29px;
  letter-spacing: -0.3px;
  text-align: center;
  color: #313131;
}

.team__block-intro {
  font-family: Merriweather;
  font-style: italic;
  font-size: 18px;
  line-height: 140%;
  text-align: center;
  color: #666666;
}

.team__block-description {
  display: none;
}

.team__block-image:hover .team__block-description {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  font-size: 14px;
  line-height: 19px;
  text-align: center;
  position: absolute;
  width: 100%;
  height: 100%;
  background: #000000b3;
  left: 0;
  top: 0;
  border-radius: 10px;
  color: #ffffff;
  padding: 0 10px;
}

.team__block-social {
  text-align: center;
  color: #1caaba;
}
.team__block-social .team__block-dot {
  color: #000000;
}
.team__block-social a {
  font-size: 14px;
  line-height: 19px;
  color: #1caaba;
}